button{
    
    padding: 1em;
    border-radius: 1em;
    color: white;
    cursor: pointer;
    
}

button:hover{
    background-color: cyan;
    color: black;
}

button.darkMode{
    background-color: red;
    border: 1px solid yellow;

}

button.lightMode{
    background-color: peru;
    border: 1px solid yellow;
}