.container {
    width: 15em;
    margin: 0 0.2em;
    float: left;
    position: relative;
}

#deletebutton{
    border: 2px solid yellow;
    padding: 5px;
    margin: 5px;
    position: absolute;
    z-index: 1;
    right: 0;
}

#editbutton{
    border: 2px solid yellow;
    padding: 5px 10px;
    margin: 5px;
    position: absolute;
    z-index: 1;
    right: 30px;
}

button.lightMode{
    background-color: peru;
    border: 1px solid yellow;
}

button.darkMode{
    background-color: red;
    border: 1px solid yellow;
}

.pic img{
    width: 100%;
}

.title{
    font-weight: bold;
    margin-bottom: 1em;
    color: white;
}

.channel, .views{
    font-size: small;
    color:#ccc;
    margin-bottom: 0.2em;
}

.views span{
    padding: 0 0.5em;
}



